.jumbotron {
  padding: 100px;
  background-image: url(https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/book-quotes-1531945007.jpg?crop=1.00xw:0.753xh;0,0.247xh&resize=980:*);
  background-size: cover;
  background-position: center;
  color: rgb(58, 58, 65);
  font-family: 'Stylish', sans-serif;
}
.card{
  font-family: 'Stylish', sans-serif; 
}
.card-header{
  background: -webkit-gradient(linear, left top, right top, from(rgb(165, 215, 200)), to(rgb(124, 201, 197)));
  background: linear-gradient(to right, rgb(165, 215, 200), rgb(124, 201, 197))
}
.list-group-item p {
  font-size: 16px;
  font-family: 'Stylish', sans-serif;
}

.btn-container {
  float: right;
  font-family: 'Stylish', sans-serif; 
}


@media (max-width: 767px) {
  .btn-container {
    margin-bottom: 15px;
  }
}
.navbar-brand{
  color: white;
  font-family: 'Stylish', sans-serif;
}
